import * as React from 'react';
import type { SinglePilet } from 'piral-base';

const DemoDiv = ({ children }) => (
    <div>
        {children}
    </div>
  )

// allow-unused-export -- pulled in via require()
export const demoPilet: SinglePilet = {
	name: 'demopilet',
	version: '0.0.1',
	spec: 'v2',
	dependencies: {},
	config: {},
	basePath: '',
	link: 'fakelink',
	setup(api) {
		api.registerExtension('dashboard-splash', () => (
			<DemoDiv>This came through a pilet extension slot</DemoDiv>
		));
	},
};